const SetCheckboxes = (form, data)=>{
    Object.values(data).map(( value, key )=>{
        let name = Object.keys(data)[key];
        let checkbox = form.querySelector(`[type='checkbox'][name='${name}']`);
        
        if(checkbox === null){ return; }
        if(value === true){
            checkbox.checked = true;
        }
    })
    
}

export {SetCheckboxes}