<script>
import { AppForms } from "../../libraries/AppForms";
import { Connect3Pod } from "../../libraries/Connect3Pod";
import { selectData } from "../../libraries/selectData";
import { SetCheckboxes } from "../../libraries/SetCheckboxes";



export let user;
export let plans;
let form = null;
let inputDefault;
let activatorElement = null;
let patnersComissions = user.patnersComissions ?? {};
let defaultPercent = 20;


let post_data = {};
patnersComissions = typeof patnersComissions === "string" ? JSON.parse(patnersComissions) : patnersComissions;
if(patnersComissions === null || patnersComissions === false){
    patnersComissions = [];
}
post_data = {
    plans: patnersComissions?.plans || {},
};
if(Array.isArray(post_data.plans)){
    let object = {};
    post_data.plans.map((data)=>{
        object[data.plan] = data;
    });
    post_data.plans = object;
    post_data = post_data;
    patnersComissions.plans = post_data.plans;
}
user = user;
patnersComissions = patnersComissions;
defaultPercent = patnersComissions.defaultPercent ?? 20;
let isActive = patnersComissions.status ?? false;

$: {
    if(form !== null){
        SetCheckboxes(form, patnersComissions);
    }
}



function addPercentage(){
    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    let plan = selectData(plans, "__id__", data.plan);

    post_data.plans[data.plan] = {
        plan: plan.__id__,
        membership_type: plan.membership_type,
        comissionPercent: data.percentPlan
    };
    post_data = post_data;
    
    patnersComissions.plans = post_data.plans;
    user = user;
    patnersComissions = patnersComissions;
}



function removePercentage(id){
    delete patnersComissions.plans[id];
    user = user;
    patnersComissions = patnersComissions;
}


function submit(form){
    let data_form = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    let data = {
        patnersComissions: post_data,
    }
    
    data.patnersComissions.plans = data.patnersComissions.plans
    data.public_key = user.public_key;
    data.defaultPercent = data_form.defaultPercent;
    data.status = (data_form?.status?.active ?? null) === null ? false : true;
  
    Connect3Pod(HOST+"/private/api/v1/superuser/AffiliateUsers/updatePatnersComissions", {
        data: data,
        ready: (data)=>{
            data = JSON.parse(data[0]);
            toast(data.message, data.status)
        }
    })
}


let membership_names = {}

plans = plans.filter(( plan )=>{
    let type = plan.membership_type
    switch (type) {
        case "funnels":
            plan.membership_name = "Funnels"
        break;
        case "designs":
            plan.membership_name = "Diseño Gráfico"
        break;
        case "campaigns":
            plan.membership_name = "Campañas"
        break;
        default:
        plan.membership_name = "Sin categoría"
        break;
    }
    return plan
})



</script>
<section>
    <header class="columns is-multiline">
        <div class="column is-12">
            <h2 class="has-text-centered is-size-6 fw600 has-text-purple">
                Módulo para patners 
                <br>
                <span class="fw600 is-size-7">
                    ({user.first_name} {user.last_name})
                </span>
            </h2>
        </div>
        <div class="column is-12">
            <h2>
                
            </h2>
        </div>
    </header>
    <main>
        <form class="columns is-multiline is-centered" action="POST" bind:this="{form}" on:submit|preventDefault="{(e)=>{submit(e.target)}}">  
            <div class="field column is-12">
                <div class="field has-text-centered">
                    <input id="switch-patner" type="checkbox" bind:this="{activatorElement}" value="active" name="status" class="switch is-purple is-rounded formElements">
                    <label for="switch-patner">Activar</label>
                </div>
                <h2 class="has-text-purple is-size-6 fw400 has-text-centered">
                    <div class="tag is-purple is-rounded">
                        Plan de comisiones (Patners)
                    </div>
                </h2>                
                <p class="has-text-justified">
                    Establece cuánto recibirá de comisión los subafiliados del patner
                </p>
            </div>
            <div class="column is-12">
                <div class="field">
                    <label for="">Porcentaje de comisión</label>
                    <div class="control has-text-centered has-icons-left">
                        <input bind:this="{inputDefault}" value="{defaultPercent}" name="defaultPercent" type="number" inputmode="numeric" class="input is-rounded formElements" placeholder="Default">
                        <span class="icon is-small is-left">
                            <i class="fas fa-percent"></i>
                        </span> 
                    </div>
                </div>
            </div>                     
        </form>
        <br>
        <div class="buttons is-centered">
            <button type="submit" on:click="{()=>{submit(form)}}" class="button is-success fw600 is-rounded has-text-white">
                Guardar
            </button>
        </div>
    </main>
</section>
<style>
.box-plans-added{
    position: relative;
}
.delete-item{
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
}
</style>