<script>
import DiscountReferrals from './DiscountsReferrals/DiscountReferrals.svelte';
import Tabs from './../components/Elements/Tabs.svelte';
import AffiliatePlans from './AffiliatePlans.svelte';
import SimpleModal from './../components/Modals/SimpleModal.svelte';
import { selectData } from '../libraries/selectData';
import { AppForms } from '../libraries/AppForms';
import PatnersComissions from './PatnersComissions/PatnersComissions.svelte';
let trigger = null;
let form = null;
let affiliates_config = (typeof CoreAffiliate === "undefined") ? {} : CoreAffiliate;
let container = document.querySelector(`.form-affiliate-superuser-3pod`);
let triggerElement = container.querySelector(`.trigger-form`);


CoreAffiliate.container = container
CoreAffiliate.init = function(data){

    triggerElement.click();
}

let usersTest = [];
let plansTest = [];
let users = (affiliates_config?.users) === null ? usersTest : affiliates_config?.users;
let plans = (affiliates_config?.plans) === null ? plansTest : affiliates_config?.plans;


let inputDefault;
let name;
let public_key;
let defaultPercent;
let user;
let post_data = {};
CoreAffiliate.init = function(data){
    users = (affiliates_config?.users) === null ? usersTest : affiliates_config?.users;
    plans = (affiliates_config?.plans) === null ? plansTest : affiliates_config?.plans;
    
    name = data.name ?? "General";
    public_key = data.public_key ?? "default";
    defaultPercent = data.defaultPercent ?? "20";
    user = selectData(users, "public_key", public_key);
    if(typeof user.public_key !== "undefined"){
        name = user.email;
    }
    user.affiliatePlan = user?.affiliatePlan ?? [];
    user.affiliatePlan = typeof user.affiliatePlan === "string" ? JSON.parse(user.affiliatePlan) : user.affiliatePlan;
    if(user.affiliatePlan === null || user.affiliatePlan === false){
        user.affiliatePlan = [];
    }
    user = user;
    user.affiliatePercent = user?.affiliatePercent ?? defaultPercent;
    post_data = {
        affiliatePercent: user.affiliatePercent,
        plans: user.affiliatePlan || {},
    };
    triggerElement.click();
}



triggerElement.addEventListener("click", ()=>{
    trigger.click();
})


function removePercentage(id){
    delete user.affiliatePlan[id];
    user = user;
}

function addPercentage(form){
    
    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    let plan = selectData(plans, "__id__", data.plan);
    post_data.affiliatePercent = data.affiliatePercent;
    user.affiliatePercent = data.affiliatePercent;
    if(typeof user.affiliatePlan === "string"){
        user.affiliatePlan = JSON.parse(user.affiliatePlan);
    }
    post_data.plans[data.plan] = {
        plan: plan.__id__,
        membership_type: plan.membership_type,
        affiliatePercent: data.percentPlan
    };
    post_data.public_key = user.public_key;

    user.affiliatePlan = post_data.plans;

    
    user = user;
}

function submit(){
    CoreAffiliate.submit = CoreAffiliate.submit||function(data){
        console.log(data);
    };
    let AffiliatePlans = {};
    Object.values(user.affiliatePlan).map((af_plan, key)=>{
        let id = Object.keys(user.affiliatePlan)[key];
        delete af_plan.plan_object;
        AffiliatePlans[id] = af_plan;
    })
    CoreAffiliate.submit({
        affiliatePercent: inputDefault.value,
        affiliatePlan: AffiliatePlans,
        public_key: user.public_key,
    });
}

let selectedTab = "comissions"
function onSelect(id){
    selectedTab = id;
}
</script>
<section class="CoreAffiliateForm">
    <SimpleModal let:active={active} styles="min-width:320px; max-width:1440px; width:90vw;" let:activateModal={activateModal} closeOnClickOverlay={false}>
        <svelte:fragment slot="trigger">
            <div class="trigger-form" bind:this="{trigger}"></div>
        </svelte:fragment>
        <svelte:fragment slot="content">
            <div class="columns is-multiline is-centered">
                <div class="column is-8">
                    <div class="columns is-multiline is-centered">
                        <div class="column is-12">
                            <Tabs items={[
                                {id: "comissions", content: "Comisiones"},
                                {id: "discounts", content: "Descuentos (Referidos)"},
                                {id: "patners", content: "Patners - Comisiones"},
                            ]} selected={selectedTab} onSelect={onSelect}  />
                        </div>
                    </div>
                    {#if selectedTab === "comissions"}
                        <section class="CoreAffiliateForm">
                            <header>
                            <h2 class="has-text-dark is-size-5 fw600 has-text-centered">
                                    Establecer porcentaje de referido
                                    <br>
                                    {name}
                            </h2>
                            </header>
                            <main>
                                <form class="columns is-multiline is-centered" action="POST" bind:this="{form}" on:submit|preventDefault="{(e)=>{submit(e.target)}}">                        
                                    <div class="field column is-12">
                                        <label for="" class="fw600 label-text">Porcentaje por default</label>
                                        <div class="control has-text-centered has-icons-left">                                
                                            <input type="number" step="any" bind:this="{inputDefault}" value="{defaultPercent}" name="affiliatePercent" placeholder="%" class="input formElements is-rounded is-hovered" min="1" max="99" required>
                                            <span class="icon is-small is-left">
                                                <i class="fas fa-percent"></i>
                                            </span>                                
                                        </div>
                                    </div>
                                    {#if public_key !== "default"}
                                        <div class="field column is-12">
                                            <h2 class="has-text-dark is-size-6 fw600 has-text-centered">
                                                Plan de comisiones
                                        </h2>
                                        <div class="explain">
                                                <p class="has-text-justified">
                                                    Esteablece un plan de afiliados o comisiones para este usuario, de manera que cada plan creado en 3Pod y que aparezca aquí disponible, podrá tener un porcenjate de ganancia distinto.
                                                    <br><br>
                                                    Busca un plan y establece un porcentaje, el resto de planes adoptarán el porcentaje global del sistema o del usuario (Establecido más arriba)
                                                </p>
                                        </div>
                                        </div>
                                        <AffiliatePlans 
                                        user={user} 
                                        addPercentage={addPercentage} 
                                        plans={plans} 
                                        form={form} 
                                        defaultPercent={defaultPercent} />
                                    {/if}                       
                                </form>     
                                {#if user.affiliatePlan.length > 0}
                                    <h3 class="has-text-centered fw600 has-text-info">
                                        Planes con porcentaje personalizado establecidos:
                                    </h3>
                                {/if}
                                {#if active === true}
                                    <div class="columns is-multiline is-centered">
                                        {#each Object.values(user.affiliatePlan).filter((plan_added)=>{
                                            plan_added.plan_object = selectData(plans, "__id__", plan_added.plan)
                                            return plan_added
                                        }) as plan}
                                            <div class="column is-12">
                                                <div class="box box-plans-added">
                                                    <div class="delete has-background-danger delete-item" on:click="{()=>{removePercentage(plan.plan_object.__id__)}}"></div>
                                                    <div class="columns is-multiline">
                                                        <div class="column">
                                                            Plan: <strong>
                                                                {plan.plan_object.name_plan}
                                                            </strong>
                                                        </div>
                                                        <div class="column">
                                                            <p class="has-text-primary fw600">
                                                                + {plan.affiliatePercent}%
                                                            </p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        {/each}
                                    </div>
                                {/if}
                                <p class="has-text-centered is-size-7">
                                    Los planes a los que no se les establezca un porcentaje, mantendrán el porcentaje por default.
                                </p>
                                <br>
                                <div class="buttons is-centered">
                                    <button type="submit" on:click="{()=>{submit(form)}}" class="button is-success fw600 is-rounded has-text-white">
                                        Guardar
                                    </button>
                                </div>
                            </main>
                        </section>
                    {/if}
                    {#if selectedTab === "discounts"}
                        <DiscountReferrals plans={plans} user={user} />
                    {/if}
                    {#if selectedTab === "patners"}
                        <PatnersComissions plans={plans} user={user} />
                    {/if}
                </div>
            </div>
        </svelte:fragment>
    </SimpleModal>
</section>
<style>
header{
    margin-bottom: 1rem;
}
.explain{
    max-width: 720px;
    display: flex;
    justify-content: center;
    margin: auto;
}
.box-plans-added{
    position: relative;
}
.delete-item{
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
}
</style>