<script>
import { selectData } from "../../../../libraries/selectData";

export let selectedPlans = [];
export let removePercentage;
export let plans;
let parsedPlans = ()=>{
    return Object.values(selectedPlans);
}
$: {
    if(selectedPlans){
        selectedPlans = parsedPlans();        
    }
}

</script>
{#each selectedPlans as plan}
    <div class="column is-12">
        <div class="box box-plans-added">
            <div class="delete has-background-danger delete-item" on:click="{()=>{removePercentage(plan.plan)}}"></div>
            <div class="columns is-multiline">
                <div class="column">
                    {selectData(plans, "__id__", plan.plan).membership_name}: <strong>
                        {selectData(plans, "__id__", plan.plan).name_plan}
                    </strong>
                </div>
                <div class="column">
                    <p class="has-text-success fw600">
                        - {plan.discountPercent}% de Descuento
                    </p>
                    <p class="fw400 is-size-7 has-text-info">
                        Duración: 
                        {#if plan.duration === "once"}
                            Una sola vez
                        {/if}
                        {#if plan.duration === "repeating"}
                            Varios meses
                            ({plan.duration_in_months})
                        {/if}
                        {#if plan.duration === "forever"}
                            Siempre
                        {/if}
                    </p>
                </div>
            </div>
        </div>
    </div>
{/each}
<style>
.box-plans-added{
    position: relative;
}
.delete-item{
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
}
</style>