<script>
export let items = [];
export let selected = null;
export let onSelect = function(){};
let tab;

function changeTab(id, e){
    let node = e.target;
    [...tab.querySelectorAll(".svelte-tab")].map(( element )=>{
        element.classList.remove("is-active");
    })
    node.classList.add("is-active");
    onSelect(id);
}
/**
 * Example Items
 * 
    [
        {id: "comissions", content: "Comisiones"},
        {id: "discounts", content: "Descuentos (Referidos)"},
    ]
*/
</script>
<div class="tabs svelte-tabs" bind:this="{tab}">
    <ul>
        {#each items as item}
            {#if item.id === selected}
                <li on:click={(e)=>{changeTab(item.id, e)}} class="is-active svelte-tab"><a href="#tab-{item.id}">{item.content}</a></li>
                {:else}
                    <li on:click={(e)=>{changeTab(item.id, e)}}><a class="svelte-tab" href="#tab-{item.id}">{item.content}</a></li>
            {/if}
        {/each}
    </ul>
</div>