</script>
{#if typeof user.public_key !== "undefined"}
        <div class="column is-12">
            <div class="field has-addons is-justify-content-center">
                <div class="control" style="width: 40%;">
                    <div class="select is-rounded">
                        <select name="plan" class="formElements">
                            {#each Object.keys(memberships) as membership, key}
                                {#each Object.values(memberships)[key].plans as plan}
                                    <option data-membership={plan.membership_type} value="{plan.__id__}">{membership_names[plan.membership_type]} - {plan.name_plan}</option>
                                {/each}
                            {/each}
                        </select>
                    </div>
                </div>
                <div class="control has-icons-left">
                    <input type="number" name="percentPlan" value="{defaultPercent}" inputmode="numeric" class="input is-rounded formElements" min="1" max="99" step="any" placeholder="%">
                    <span class="icon is-small is-left">
                        <i class="fas fa-percent"></i>
                    </span>
                </div>
                <div class="control">
                    <button on:click="{()=>{addPercentage(form)}}" type="button" class="button is-primary is-outlined is-rounded">
                        Agregar
                    </button>
                </div>
            </div>
        </div>
    {:else}
        Hay un error con la public key de este usuario
{/if}