<script>
export let form;
export let durationElementChange;
export let plans;
export let duration_in_months_input;
export let addPercentage;
let durationElement;
</script>
<div class="column is-12">
    <div class="box">
        <h2 class="has-text-dark is-size-6 fw600 has-text-centered">
            Plan de descuentos
        </h2>
        <br>
        <div class="columns is-multiline">
            <div class="column is-6">
                <div class="field">
                    <div class="control is-fullwidth">
                        <div class="select is-rounded is-fullwidth">
                            <select name="plan" class="formElements">
                                {#each plans as plan}
                                    <option data-membership={plan.membership_type} value="{plan.__id__}">{plan.membership_name} - {plan.name_plan}</option>
                                {/each}
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            <div class="column is-6">
                <div class="field">
                    <div class="control has-icons-left">
                        <input type="number" name="percentPlan" value="20" inputmode="numeric" class="input is-rounded formElements" min="1" max="99" step="any" placeholder="%" required>
                        <span class="icon is-small is-left">
                            <i class="fas fa-percent"></i>
                        </span>
                    </div> 
                </div>
            </div>
            <div class="column">
                <div class="field">
                    <div class="control is-fullwidth">
                        <div class="select is-rounded is-fullwidth">
                            <select on:change={(()=>{durationElementChange(durationElement)})} name="duration" class="formElements" bind:this={durationElement}>
                                <option value="once">Una sola vez</option>
                                <option value="repeating">Varios meses</option>
                                <option value="forever">Siempre</option>
                            </select>
                        </div>
                    </div>
                </div>
            </div>
            {#if duration_in_months_input === "active"}
                <div class="column is-6">
                    <div class="field">
                        <div class="control has-icons-left">
                            <input type="number" name="duration_in_months" value="1" inputmode="numeric" class="input is-rounded formElements" min="1" max="360" required>
                            <span class="icon is-small is-left">
                                <i class="fas fa-calendar-week"></i>
                            </span>
                        </div>
                    </div>
                </div>
            {/if}
            <div class="column is-12">
                <div class="field is-justify-content-center is-flex">
                    <div class="control">
                        <button on:click="{()=>{addPercentage(form)}}" type="button" class="button is-primary is-outlined is-rounded">
                            Agregar
                       </button>
                    </div>
                </div> 
            </div>
        </div>
    </div>
</div>