<script>
	import DiscountPlansList from './../../components/Elements/superuser/DiscountPlans/DiscountPlansList.svelte';
	import DiscountPlansForm from './../../components/Elements/superuser/DiscountPlans/DiscountPlansForm.svelte';
import { AppForms } from "../../libraries/AppForms";
import { Connect3Pod } from "../../libraries/Connect3Pod";
import { selectData } from "../../libraries/selectData";



export let user;
export let plans;
let form = null;
let durationElement = null;
let duration_in_months_input = false;

function durationElementChange(node){
    if(node.value === "repeating"){
        duration_in_months_input = "active";
    }else{
        duration_in_months_input = false;
    }
}

user.referralDiscounts = user.referralDiscounts ?? [];
let post_data = {};
user.referralDiscounts = typeof user.referralDiscounts === "string" ? JSON.parse(user.referralDiscounts) : user.referralDiscounts;
if(user.referralDiscounts === null || user.referralDiscounts === false){
    user.referralDiscounts = [];
}
post_data = {
    plans: user?.referralDiscounts?.plans || {},
};
if(Array.isArray(post_data.plans)){
    let object = {};
    post_data.plans.map((data)=>{
        object[data.plan] = data;
    });
    post_data.plans = object;
    post_data = post_data;
    user.referralDiscounts.plans = post_data.plans;
}
user = user;


function addPercentage(){
    let data = AppForms.retrieve({
        form: form,
        targets: ".formElements",
    })
    let plan = selectData(plans, "__id__", data.plan);
    let duration_in_months = data?.duration_in_months ?? null;

    post_data.plans[data.plan] = {
        plan: plan.__id__,
        membership_type: plan.membership_type,
        duration: data.duration,
        discountPercent: data.percentPlan
    };
    
    if(duration_in_months !== null){
        post_data.plans[data.plan].duration_in_months = duration_in_months;
    }
    post_data = post_data;
    
    user.referralDiscounts.plans = post_data.plans;
    user = user;
}

let selectedPlans = user?.referralDiscounts?.plans || [];

$: {
    if(user){
        selectedPlans = user?.referralDiscounts?.plans || [];
        let ordered = {};
        Object.values(selectedPlans).map(( plan )=>{
            ordered[plan.plan] = plan;
        })
        selectedPlans = ordered;
    }
}

function removePercentage(id){
    delete user.referralDiscounts.plans[id];
    user = user;
}


function submit(){
    let data = {
        referralDiscounts: post_data,
    }
    data.referralDiscounts.plans = Object.values(data.referralDiscounts.plans)
    data.public_key = user.public_key;

    Connect3Pod(HOST+"/private/api/v1/superuser/AffiliateUsers/updateReferralDiscounts", {
        data: data,
        ready: (data)=>{
            data = JSON.parse(data[0]);
            toast(data.message, data.status); 
        }
    })
}


let membership_names = {}


plans = plans.filter(( plan )=>{
    let type = plan.membership_type
    switch (type) {
        case "funnels":
            plan.membership_name = "Funnels"
        break;
        case "designs":
            plan.membership_name = "Diseño Gráfico"
        break;
        case "campaigns":
            plan.membership_name = "Campañas"
        break;
        default:
        plan.membership_name = "Sin categoría"
        break;
    }


    return plan
})


</script>
<section>
    <header class="columns is-multiline">
        <div class="column is-12">
            <h2 class="has-text-centered is-size-6 fw600 has-text-info">
                Descuentos para los referidos de
                <br>
                <span>
                    {user.email}
                </span>
            </h2>
        </div>
    </header>
    <main>
        <form class="columns is-multiline is-centered" action="POST" bind:this="{form}" on:submit|preventDefault="{(e)=>{submit(e.target)}}">  
            <DiscountPlansForm 
                form={form}
                durationElementChange={durationElementChange}
                plans={plans}
                duration_in_months_input={duration_in_months_input}
                addPercentage={addPercentage}
            />         
        </form>     
        {#if user.referralDiscounts.length > 0}
            <h3 class="has-text-centered fw600 has-text-info">
                Planes con porcentaje personalizado establecidos:
            </h3>
        {/if}
        <div class="columns is-multiline is-centered">
            <DiscountPlansList 
                selectedPlans={selectedPlans}
                removePercentage={removePercentage}
                plans={plans}
            />
        </div>
        <br>
        <div class="buttons is-centered">
            <button type="submit" on:click="{()=>{submit(form)}}" class="button is-success fw600 is-rounded has-text-white">
                Guardar
            </button>
        </div>
    </main>
</section>
